.contacts-info {
	display: flex;

	@include max(md) {
		flex-direction: column;
	}

	&__left {
		width: 49%;
		//border: 1px solid $gray;
		border-radius: 5px 5px 0 5px;
		box-sizing: border-box;

		@include max(md) {
			width: 100%;
		}
	}

	&__right {
		width: 51%;

		@include max(md) {
			width: 100%;
			height: 350px;
		}

		@include max(sm) {
			height: 300px;
		}

		iframe {
			width: 100%;
			height: 100%;
		}
	}

	&__content {
		max-width: 620px;
		width: 100%;
		padding: 50px 15px 12px;
		margin-left: auto;

		@include max(md) {
			margin: 0 auto;
			padding-left: 20px;
			padding-right: 20px;
		}
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-top: 29px;

		@include max(sm) {
			margin-top: 31px;
		}
	}

	&__item {
		margin-bottom: 35px;

		@include max(sm) {
			margin-bottom: 30px;
		}

		&--worktime,
		&--social {
			@include max(sm) {
				order: 1;
			}
		}

		&:nth-child(odd) {
			width: 55%;

			@include max(sm) {
				width: 100%;
			}
		}

		&:nth-child(even) {
			width: 45%;

			@include max(sm) {
				width: 100%;
			}
		}
	}

	&__hint {
		font-size: 24px;
		line-height: 29px;
		font-weight: 500;

		@include max(sm) {
			font-size: 18px;
			line-height: 21px;
		}
	}

	&__text {
		font-size: 16px;
		line-height: 20px;
		font-style: normal;
		margin-top: 21px;

		@include max(sm) {
			margin-top: 16px;
		}

		&--socials {
			a {
				@include max(sm) {
					display: inline-block;
					vertical-align: top;
					width: 20px;

					svg {
						max-width: 100%;
						height: auto;
					}
				}
			}
		}

		a {
			transition: opacity 0.3s ease;

			@include hover {
				opacity: 0.8;
			}
		}

		p {
			+ p {
				margin-top: 10px;

				@include max(sm) {
					margin-top: 10px;
				}
			}
		}

		&--socials {
			margin-top: 15px;

			a {
				transition: opacity 0.3s ease;

				@include hover {
					opacity: 0.8;
				}

				+ a {
					margin-left: 5px;
				}
			}

			svg {
				fill: $blue;
			}
		}
	}

	&__contacts-item {
		display: flex;
		align-items: center;

		svg {
			flex-shrink: 0;
			margin-right: 10px;
		}
	}
}
